import { 
  listElement, 
  input 
} from './index';

import {
  storeTaskInLocalStorage,
  removeTaskFromLocalStorage,
  tasks
} from './sessionStorage';

export function addTask(e) {
  e.preventDefault();
  
  const task = input.value;

  if (task.length === 0) {
    showAlert('Please fill out the task', 'warning');
    return;
  } else if (task.length < 3 || e.keyCode === 13 && e.target.value.length < 3) {
    showAlert('Task can\'t be shorter than 3 symbol', 'warning');
    return;
  } else {
    showAlert('Task Added!', 'success');
    renderTask(task);
    storeTaskInLocalStorage(task);
    input.value = '';
  }
}

export function renderTask(task) {
  const taskElement = document.createElement('div');
  taskElement.classList.add('task');
  taskElement.innerHTML = task;

  const taskDelete = document.createElement('button');
  taskDelete.classList.add('task__delete-btn');
  taskDelete.innerHTML = '<i class="fas fa-times"></i>';

  taskElement.appendChild(taskDelete);
  listElement.appendChild(taskElement);
}

export function renderFromStorage() {
  tasks.forEach(function(task) {
    renderTask(task);
  });
}

export function removeTask(e) {
  if (e.target.parentElement.classList.contains('task__delete-btn')) {
    if (confirm('Are You Sure?')) {
      e.target.parentElement.parentElement.remove();

      removeTaskFromLocalStorage(e.target.parentElement);
      showAlert('Task Removed!', 'success');
    }
  }
}

function showAlert (message, className) {
  const messageContainer = document.querySelector('.message__container');
  messageContainer.innerHTML = `<span class="${className} message">${message}</span>`;
  
  setTimeout(function () { 
    if (messageContainer.querySelector(`.${className}`) !== null) {
      messageContainer.querySelector(`.${className}`).remove();
    }   
  }, 1500);
}